import Icon from '@mdi/react';
import moment from 'moment';
import { mdiNpm, mdiTelegram, mdiGithub, mdiInstagram, mdiEmail, mdiTwitter, mdiGitlab, mdiBitbucket } from '@mdi/js';

const Header = () => {
  const currentYearOld = () => {
    const birthDate = moment(new Date(1996, 0, 17, 3, 30));
    const now = moment(new Date());

    return now.diff(birthDate, 'year');
  };

  return (
    <header className="header">
      <div className="header__avatar">
        <img className="header__avatar-img"
          src="https://hsto.org/getpro/moikrug/uploads/user/100/024/364/0/avatar/medium_b927fdb65bb090c8314f17074175b9ee.jpg"
          alt="Avatar"
        />
      </div>
      <div className="header__description">
        <div className="header__description-text">
          <h1>Shekhovtsov Kirill, {currentYearOld()} years old</h1>
          <h2>Frontend (Fullstack) Developer, TeamLead</h2>
          <div className="header__icons">
            <div className="header__icons-icon" title="E-Mail">
              <a href="#" target="_blank">
                <Icon path={mdiEmail} size={1.7} />
              </a>
            </div>
            <div className="header__icons-icon" title="Telegram">
              <Icon path={mdiTelegram} size={1.7} />
            </div>
            <div className="header__icons-icon" title="Twitter">
              <Icon path={mdiTwitter} size={1.7} />
            </div>
            <div className="header__icons-icon" title="Instagram">
              <Icon path={mdiInstagram} size={1.7} />
            </div>
            <div className="header__icons-icon" title="GitHub">
              <Icon path={mdiGithub} size={1.7} />
            </div>
            <div className="header__icons-icon" title="GitLab">
              <Icon path={mdiGitlab} size={1.7} />
            </div>
            <div className="header__icons-icon" title="BitBucket">
              <Icon path={mdiBitbucket} size={1.7} />
            </div>
            <div className="header__icons-icon" title="NPM">
              <Icon path={mdiNpm} size={1.7} />
            </div>
          </div>
        </div>

      </div>
    </header >);
}

export default Header;
